import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { InlineNotification } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-alert status="error" content="Please contact system administrator." prefix="error"/>
*/
const SrsAlert = function (props, ref) {
    const { status, icon, prefix, content, link_label, link_href } = props;

    return (
      <InlineNotification
        kind={status}
        title={prefix}
        subtitle={content}
        lowContrast
        iconDescription={icon}
        footer={
            link_href && (
            <a href={link_href} className="bx--link">
              {link_label || link_href}
            </a>
          )
        }
      />
    );
}

const styles = `
    
`
const props = [{
    name: "status",
    defaultValue: "error",
    type: "string"
}, {
    name: "icon",
    defaultValue: "",
    type: "string"
}, {
    name: "prefix",
    defaultValue: " ",
    type: "string"
}, {
    name: "content",
    defaultValue: "",
    type: "string"
}, {
    name: "link_label",
    defaultValue: "More Info",
    type: "string"
}, {
    name: "link_href",
    defaultValue: "#",
    type: "string"
}];

const slots = []; 

const WrappedComponent = wrapper(React.forwardRef(SrsAlert), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };